#game {
	// background: #353336;
	width: 100vw;
	height: 100vh;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

#game-canvas {
	display: block;
	border-radius: 5px;
}

.game-header {
	padding-top: 1vmin;
	padding-bottom: 1vmin;

	display: flex;
	justify-content: space-between;
}
.game-score {
	padding: 10px 15px;
	min-width: 160px;
	height: 50px;
	background: black;
	border-radius: 5px;

	display: flex;
	justify-content: center;
	align-items: center;
}
.score-count {
	font-size: 3vmin;
	color: #d21855;
	font-weight: bold;
}

.canvas-wrapper {
	background-color: rgba($color: #161618, $alpha: 0.7);
	border-radius: 5px;
	border: 2px solid #161618;
	box-shadow: 0px 0px 40px 10px #161618;

	background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.2'%3E%3Ccircle cx='8' cy='8' r='4' fill='%23525053'/%3E%3C/g%3E%3C/svg%3E%0A");
}
